import { h, Component } from 'preact';
import { useEffect } from "preact/hooks";
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import linkstate from 'linkstate';
import Markdown from 'preact-markdown';
import Header from './header';
import RaceText from './raceText';

import mu from '../../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

function PageTitle(title = '') {
  useEffect(() => {
    document.title = `Homerun Race - Race where you live`;
  }, [title]);
  return <h1>{title}</h1>;
}

const initialState = {
	isLoading: false,
	email: '',
	submenu: 'ads',
	jwtToken: util.getJwtToken(),
};

@observer
class PublicRace extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	async loadRace(props = this.props) {
		this.setState({ isLoading: true });
		const { raceStore } = props.stores;
		const query = {};
		await raceStore.load({ query, sort: '-startDate' });
		raceStore.countAll();
		this.setState({ isLoading: false });
	}

	async loadNews(props = this.props) {
		this.setState({ isLoading: true });
		const { newsStore, userStore } = props.stores;
		const { isAdmin } = userStore;
		const query = {
			tag: props.tag,
		};
		if (isAdmin) {
			query.showall = 1;
		}
		await newsStore.load({ query });
		this.setState({ isLoading: false });
	}

	toggleShowNews = () => {
		const { showNews } = this.state;
		this.setState({
			showNews: !showNews,
		});
	}

	componentDidMount() {
		this.loadRace();
		this.loadNews();
	}

	componentWillReceiveProps(nextProps) {
		this.loadRace(nextProps);
		this.loadNews(nextProps);
	}

	render() {
		const { isInApp } = this.props;
		const { appState, userStore, raceStore, newsStore } = this.props.stores;
		const { allUsers, isAdmin, user, notifications = [] } = userStore;
		const { races } = raceStore;
		const { newsList } = newsStore;
		const news = newsList && newsList[0] ? newsList[0] : null;
		const { darkmode, language = 'en' } = appState;
		const { showNews } = this.state;

		return (
			<>
				<Header stores={this.props.stores} isInApp={isInApp} />

				<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>

					{!news && <div class='d-flex justify-content-center my-5 py-5'>
						<div class='spinner-border text-primary' role='status'>
							<span class='sr-only'>Loading...</span>
						</div>
					</div>}

					{news && <div class='row clearfix' style='min-height: 450px;'>
						<div class='col-12'>
							<div class='jumbotron-thumb d-flex align-items-center'>
								{(news.images && news.images[0]) ? <img src={news.images[0].s3XXLargeLink} class='img-fluid border rounded-lg' style='min-width: 100%' /> : ''}
							</div>

							<div class='jumbotron py-2'>
								<h1 class='display-4'>{isAdmin && <i class={`fas fa-eye${news.active ? ' text-success' : '-slash text-danger'} float-right`} />} {news.title}</h1>

								<p class='lead'>
									<Markdown markdown={mu.replaceImages(news.ingress, news.images, language)} markdownOpts={mu.getMarkdownOptions()} />								
								</p>

								{showNews ? <>
									{/* {mu.nl2p(news.body).map((part, idx) => (
										<p 
											class='clearfix'
											dangerouslySetInnerHTML={{
												__html: replaceTags(part, news.images, idx)
											}}
										/>
									))}
									<hr /> */}
									<Markdown markdown={mu.replaceImages(news.body, news.images, language)} markdownOpts={mu.getMarkdownOptions()} />

									<button class='btn btn-secondary btn-lg' type='button' onClick={this.toggleShowNews}>
										<Text id='races.hide-details'>Skjul detaljer</Text>  <i class='fas fa-caret-down' />
									</button>
								</> : <>
									<button class='btn btn-primary btn-lg' type='button' onClick={this.toggleShowNews}>
										<Text id='races.read-more'>Les mer</Text> <i class='fas fa-caret-right' />
									</button>
								</>}
							</div>

						</div>
					</div>}
					<div class='row'>
						<div class='col-12 bg-light'>
							<Localizer>
								{PageTitle(<Text id='races.races'>Løpene</Text>)}
							</Localizer>
							{isAdmin && <>
								<a href='/race/new/edit' class='position-absolute btn btn-sm btn-primary' style='top: 0px; right: 15px;'>
									<i class='fas fa-plus-circle' /> <Text id='race.new'>Nytt løp</Text>
								</a>
							</>}
						</div>
					</div>
					<div class='row mt-3'>
						{!races && <div class='d-flex justify-content-center my-5 py-5'>
							<div class='spinner-border text-primary' role='status'>
								<span class='sr-only'>Loading...</span>
							</div>
						</div>}

						{races && races.map(race =>  {
							const today = new Date();
							let status = '';
							if (today >= new Date(race.startDate) && today <= new Date(race.endDate)) {
								status = 'race';
							} else if (today >= new Date(race.signupStartDate) && today <= new Date(race.signupEndDate)) {
								status = 'signup';
							} else if (today > new Date(race.endDate)) {
								status = 'done';
							}

							return (<>
								<div class='col-12 col-sm-6 col-md-4 mb-3'>

									<div class='card position-relative'>
										<h5 class='position-absolute' style='top: 10px; right: 10px;'>
											{status === 'signup' && <>
												<div class='spinner-grow text-primary' role='status' />
											</>}
											{status === 'race' && <>
												<div class='spinner-grow text-danger' role='status' />
											</>}
											{status === 'done' && <>
												<span><i class='fas fa-check text-success' /></span>
											</>}
										</h5>
										<h5 class='card-header'>
											{isAdmin && <i class={`fas fa-eye${race.active ? ' text-success' : '-slash text-danger'} float-right`} />}
											{race.title}
										</h5>
										<div class='list-thumb d-flex align-items-center position-relative'>
											<h5 class='position-absolute' style='top: 3px; right: 5px;'>
												{status === 'signup' && !race.liveRace && <>
													<span class='badge badge-primary border border-white ml-2'><Text id='races.signup-open'>Påmelding åpen</Text></span>
												</>}
												{status === 'race' && <>
													<span class='badge badge-danger border border-white ml-2'><Text id='races.race-in-progress'>Løp pågår</Text></span>
												</>}
												{status === 'done' && <>
													<span class='badge badge-success border border-white ml-2'><Text id='races.race-finished'>Løp avsluttet</Text></span>
												</>}
											</h5>
											{(race.images && race.images[0]) ? <img src={race.images[0].s3MediumLink} class='card-img-top' /> : ''}
										</div>
										<div class='card-body'>
											<p class='card-text'>
												<Markdown markdown={mu.replaceImages(race.ingress, race.images, language)} markdownOpts={mu.getMarkdownOptions()} />
											</p>
											{race.classes && <>
												<div class='mb-3'>
													<small class='font-weight-lighter mr-2'><Text id='races.classes'>Klasser</Text>: </small><br />
													{race.classes.sort((a, b) => a.distance - b.distance).map(currentClass => <>
														<span class='badge badge-pill badge-light mr-2 font-weight-light'>
															{currentClass.title}
															{/* <span class='badge badge-info ml-2 font-weight-light'>{currentClass.contestants.length}</span> */}
														</span>
													</>)}											
												</div>
											</>}
											<a href={`/race/${race.id}`} class='btn btn-primary stretched-link'><Text id='races.read-more'>Les mer</Text></a>
										</div>
										<div class='card-footer'>
											<small class='text-muted'>
												<Text id='races.race-days'>Løpsdager</Text>: {race.startDate && <>{util.formatDate(race.startDate, { locale: language })}</>}
												{race.endDate && <> - {util.formatDate(race.endDate, { locale: language })}</>}
											</small>
										</div>
									</div>

								</div>						
							</>);
						})}

					</div>
				</div>
			</>
		);
	}
}

export default PublicRace;
