import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

const initialState = {
};

@observer
class RaceText extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    render() {
		const { appState } = this.props.stores;
		const { text } = this.props;
		const { darkmode, language = 'en' } = appState;

        let finalText = text;

        const regex = new RegExp(`/<${language}>(.*?)<\/${language}>/`);
        const parsedText = text.replace(regex, '$1');
        if (parsedText) {
            finalText = text;
        }

		return (
			<>
                {finalText}
			</>
		);
	}
}

export default RaceText;
